@import '../../consts.scss';

.registration {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 4rem);
  padding-top: 4rem;
  color: $c4;
  input,
  button {
    width: 12rem
  }
}

.registration-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 450px;
  height: 320px;
  margin: 0 auto;
  backdrop-filter: blur(5px);
  background-color: hsla(0,0%,100%,.3);
  border-radius: 0.5rem;
  padding: 1rem 0;
}